<template>
    <div>
        <div class="login-main-outside">
            <div class="login-main-inside">
                <div class="img-div">
                    <el-image style="width: 100%; height: 100%" src="/img/login.jpg" fit="cover"></el-image>
                </div>
                <div class="login-input">
                    <div class="login-title">
                        <h1>登录test</h1>
                    </div>
                    <div class="login-welcome">
                        Welcome to 突突突
                    </div>
                    <div class="login-username-password">
                        <div class="input-title">
                            <div class="span-div">
                                <span>username</span>
                            </div>
                            <div>
                                <el-input @keydown.enter.native="login" v-model="loginParams.username"></el-input>
                            </div>
                        </div>
                        <div class="input-title">
                            <div class="span-div">
                                <span>password</span>
                            </div>
                            <div>
                                <el-input @keydown.enter.native="login" type="password" v-model="loginParams.password"></el-input>
                            </div>

                        </div>
                    </div>
                    <div class="login-btn">
                        <el-button type="info" :loading="loginLoading" @click="login">登录</el-button>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import urls from '@/api/urls'
    export default {
        data() {
            return {
                loginParams: {
                    username: null,
                    password: null
                },
                loginLoading: false
            }
        },
        methods: {

            login() {
                if (!this.loginParams.username) {
                    this.$message({
                      message: 'username 不能为空',
                      type: 'warning'
                    });
                    return;
                }
                if (!this.loginParams.password) {
                    this.$message({
                      message: 'password 不能为空',
                      type: 'warning'
                    });
                    return;
                }
                this.loginLoading = true;
                this.$axios.post(urls.user.login, this.loginParams).then(res => {
                    console.log(res);
                    localStorage.setItem("tokenName", res.data.tokenName);
                    localStorage.setItem("tokenValue", res.data.tokenValue);
                    // 跳转到首页
                    this.$router.push('/')
                }).finally(() => {
                    this.loginLoading = false;
                })
            },

            tiaozhuan() {
                this.$router.push('/ttt')
            },

            qingqiu() {
                this.$axios.get(urls.test)
            }
        }
    }
</script>

<style scoped>

    .login-main-outside {
        display: flex;
        align-items: center;
        justify-content: center;
        /*background-color: #2c3e50;*/
        height: 100vh;
    }

    .login-main-inside {
        width: 800px;
        height: 600px;
        /*background-color: #42b983;*/
        display: flex;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }

    .img-div {
        background-color: darkcyan;
        width: 55%;
    }

    .login-input {
        /*background-color: dodgerblue;*/
        width: 45%;
        display: flex;
        align-items: center;
        flex-flow: column;
        background-color: #FFFFFF;
    }

    .login-title {
        width: 100%;
        /*background-color: darkcyan;*/
        display: flex;
        justify-content: center;
        margin-top: 50px;
    }

    .login-welcome {
        /*background-color: darkkhaki;*/
        margin-top: 50px;
        display: flex;
        justify-content: center;
    }

    .login-username-password {
        height: 150px;
        width: 70%;
        /*background-color: antiquewhite;*/
        margin-top: 30px;
    }

    .input-title {
        height: 50%;
        color: #909399;
        font-size: 13px;
        /*background-color: #42b983;*/
    }

    .login-btn {
        margin-top: 30px;
        width: 70%;
    }

    .login-btn > * {
        width: 100%;
    }

    .span-div {
        margin-bottom: 5px;
        padding-left: 5px;
    }

</style>
